.loginsignup {
    width: 100%;
    background: #fce3fe;
    padding: 100px 0;
}
.loginsignup-container {
    width: 50%;
    height: 600px;
    background: #fff;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup-container h1 {
    margin: 20px 0;
}
.loginsignup-fields {
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin: 30px;
}
.loginsignup-fields input {
    height: 72px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}
.loginsignup-container button {
    width: 100%;
    height: 72px;
    color: #fff;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}
.loginsignup-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 12px;
    font-weight: 500;
}
.loginsignup-login span {
    color: #ff4141;
    font-weight: 600;
    cursor: pointer;
}
.loginsignup-agree {
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 12px;
    font-weight: 500;
}

@media (max-width: 946px) {  
    .loginsignup-fields {
        margin: 0;
    }
    .loginsignup-fields input{
        font-size: 14px ;
        height: 42px;
    }
    .loginsignup-container h1 {
        font-size: 17px;
    }
    .loginsignup-container button {
        height: 42px;
        font-size: 18px;
    }
}